"use client";

import * as React from "react";
import { YearSelector } from "@/components/year-selector";
import { getPastYears } from "@/lib/year-utils";
import { Card, CardContent } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";

interface YearFilterBarProps {
  selectedYear: number;
  currentYear: number;
  onYearChange: (year: number) => void;
  isLoading?: boolean;
}

export function YearFilterBar({
  selectedYear,
  currentYear,
  onYearChange,
  isLoading = false,
}: YearFilterBarProps) {
  // Generate list of past 5 years (current year + 5 previous years = 6 years total)
  const pastYears = React.useMemo(() => {
    return getPastYears(currentYear, 5);
  }, [currentYear]);

  return (
    <div className="rounded-lg border bg-card p-4 shadow-sm">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <span className="text-sm font-medium text-muted-foreground">Viewing:</span>
          <YearSelector 
            selectedYear={selectedYear}
            onYearChange={onYearChange}
            availableYears={pastYears}
          />
        </div>
        {isLoading && (
          <span className="text-sm text-muted-foreground">Loading...</span>
        )}
      </div>
    </div>
  );
}

